import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";

// Primary Button
export const PrimaryButton = ({ path, TextContent }) => {
  return <PrimaryLink to={path}>{TextContent}</PrimaryLink>;
};
// Styles for Primary Button
const PrimaryLink = styled(Link)`
  @media screen and (max-width: 768px) {
    padding: 1.5rem 2.5rem;
  }

  padding: 2rem 3rem;
  border: 2px solid #ffae6e;
  transition: all 300ms ease-in;
  color: #ffae6e;

  &:hover {
    background-color: #ffae6e;
    border: 1px solid #ffae6e;
    color: black;
  }
`;

// Secondary Button
export const SecondaryButton = ({ path, TextContent }) => {
  return (
    <SecondaryLink to={path}>
      {TextContent} <span>&#10230;</span>
    </SecondaryLink>
  );
};
// Styles for Secondary Button
const SecondaryLink = styled(Link)`
  letter-spacing: 1px;
  transition: all 300ms ease-in;

  &:hover {
    color: #ffae6e;
  }
`;

// Circle Button
export const CircleButton = ({ path, TextContent }) => {
  return (
    <Link to={path}>
      <CircleDiv>
        <p>{TextContent}</p>
      </CircleDiv>
    </Link>
  );
};
// Styles for Circle Button
const CircleDiv = styled.div`
  width: 20rem;
  height: 20rem;
  border-radius: 50%;
  color: black;
  background-color: #ffae6e;

  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 200ms ease-in;

  &:hover {
    transform: scale(0.95);
  }
`;

// Custom Button
export const CustomButton = ({ children, ...otherProps }) => {
  return <Button {...otherProps}>{children}</Button>;
};
// Styles for Custom Button
const Button = styled.button`
  width: 15rem;
  height: 15rem;
  border-radius: 50%;
  font-size: 1.6rem;
  background-color: #ffae6e;
  border: none;
  cursor: pointer;
  margin: 2rem 30% 0;
  transition: all 200ms ease;
  text-transform: uppercase;

  @media screen and (max-width: 768px) {
    margin: 2rem 0;
  }

  &:hover {
    transform: scale(1.05);
  }

  &:focus {
    transform: scale(1);
  }
`;
