import React, { useState } from "react";
import styled from "styled-components";
import FormInput from "../components/FormInput.js";
import { CustomButton } from "../components/Buttons.js";
import { useForm, ValidationError } from "@formspree/react";

// Contact Form
export const ContactForm = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [detail, setDetail] = useState("");

  // handle functions
  // const handleSubmit = (event) => {
  //   event.preventDefault();
  //   setName("");
  //   setEmail("");
  //   setDetail("");
  // };

  const [state, handleSubmit] = useForm("xzbyyvvg");
  if (state.succeeded) {
    return (
      <ThanksMsg>
        Thanks for your interest! Will get back to you soon. 😃
      </ThanksMsg>
    );
  }

  return (
    <StyledContactForm>
      <p>HELLO,</p>
      <form onSubmit={handleSubmit}>
        <FormInput
          type="text"
          name="fullname"
          label="MY NAME IS"
          placeholder="Enter your name here"
          onChange={(e) => setName(e.target.value)}
          value={name}
          id="fullname"
          required
        />

        <FormInput
          type="email"
          name="email"
          label="HERE IS MY EMAIL"
          placeholder="Enter your email here"
          onChange={(e) => setEmail(e.target.value)}
          value={email}
          id="email"
          required
        />

        <FormInput
          type="textarea"
          name="detail"
          label="I'M LOOKING FOR"
          placeholder="Enter your project details here"
          onChange={(e) => setDetail(e.target.value)}
          value={detail}
          id="detail"
          required
        />

        <CustomButton type="submit">Send</CustomButton>
      </form>
    </StyledContactForm>
  );
};

// Styles for contact forms
const StyledContactForm = styled.div`
  padding-top: 4rem;

  @media screen and (max-width: 768px) {
    padding-top: 2rem;
  }

  p {
    font-size: 10rem;
    padding-left: 40rem;

    @media screen and (max-width: 1200px) {
      font-size: 9rem;
    }

    @media screen and (max-width: 1200px) {
      font-size: 8rem;
    }

    @media screen and (max-width: 875px) {
      padding-left: 0;
      font-size: 7rem;
    }

    @media screen and (max-width: 768px) {
      font-size: 4rem;
    }
  }
`;

const ThanksMsg = styled.p`
  font-size: 2rem;
  font-weight: 300;
`;
