import React from "react";
import styled from "styled-components";
import { motion } from "framer-motion";

import { lineAnimation } from "../animation.js";

// PRIMARY HEADING
export const PrimaryHeading = ({ id, TextContent }) => {
  return (
    <Container>
      <H1 id={id}>{TextContent}</H1>
    </Container>
  );
};
// STYLES FOR PRIMARY HEADING
const Container = styled.div`
  padding-right: 15%;
  text-align: right;

  #two {
    &:before {
      content: "02'";
    }
  }

  #three {
    padding-right: 20%;

    @media screen and (max-width: 768px) {
      padding-right: 0;
    }
    &:before {
      content: "03'";
      left: -12%;

      @media screen and (max-width: 768px) {
        font-size: 2.5rem;
        left: 0%;
      }
    }

    @media screen and (max-width: 480px) {
      width: 100%;
    }
  }

  #four {
    &:before {
      content: "04'";
    }
  }

  @media screen and (max-width: 1200px) {
    padding-right: 14%;
  }

  @media screen and (max-width: 768px) {
    padding: 0;
    margin: 0 0;
    text-align: left;
  }
`;
const H1 = styled.h2`
  display: inline-block;
  margin-left: auto;
  color: #e5e7eb;
  font-size: 9rem;
  letter-spacing: 8%;
  text-transform: uppercase;
  font-weight: 500;
  border-bottom: 5px solid #e5e7eb;
  position: relative;

  @media screen and (max-width: 1200px) {
    font-size: 7rem;
  }

  @media screen and (max-width: 768px) {
    font-size: 5rem;
    display: block;
    border-bottom: 3px solid #e5e7eb;
  }

  @media screen and (max-width: 480px) {
    font-size: 3rem;
  }

  &:before {
    position: absolute;
    top: -8%;
    left: -8%;
    opacity: 50%;
    font-family: "Playfair Display", sans-serif;
    font-style: itallic;

    @media screen and (max-width: 1200px) {
      top: -20%;
      left: -12%;
      font-size: 5rem;
    }

    @media screen and (max-width: 1024px) {
      font-size: 4.2rem;
      top: -8%;
      left: -8%;
    }

    @media screen and (max-width: 768px) {
      font-size: 2.5rem;
      top: -70%;
      left: 0%;
    }

    font-size: 5rem;
  }
`;

// ANIMATED HEADING
export const AnimatedHeading = ({ id, TextContent }) => {
  return (
    <AnimatedH1Container>
      <AnimatedH1 id={id}>{TextContent}</AnimatedH1>
    </AnimatedH1Container>
  );
};

// STYLES FOR ANIMATED HEADING
const AnimatedH1Container = styled.div`
  overflow: hidden;

  #left {
    animation: 3s infinite linear movein alternate;
  }

  #right {
    animation: 5s infinite linear moveout alternate;
  }
`;

const AnimatedH1 = styled.h1`
  font-family: "Playfair Display", serif;
  font-size: 10rem;
  font-weight: 900;
  font-style: itallic;
  white-space: nowrap;
  -webkit-text-stroke: 1px #e5e7eb;
  color: transparent;

  @media screen and (max-width: 1200px) {
    font-size: 9rem;
  }

  @media screen and (max-width: 1024px) {
    font-size: 8rem;
  }

  @media screen and (max-width: 768px) {
    font-size: 6rem;
  }

  @media screen and (max-width: 480px) {
    font-size: 4rem;
  }
`;

// LINE HEADING
export const LineHeading = ({ TextContent }) => {
  return (
    <StyledLineHeading>
      <motion.div className="line" variants={lineAnimation}></motion.div>
      {TextContent}
    </StyledLineHeading>
  );
};

// STYLES FOR LINE HEADING
const StyledLineHeading = styled.div`
  padding: 2rem 0;
  font-size: 2rem;
  font-weight: 300;

  .line {
    width: 100%;
    background: #e5e7eb;
    height: 3px;
    margin-bottom: 2rem;
  }
`;
